export const title = "다이트로 블로그 개장";
export const postDate = "2021-03-20";
export const description = "다이트로의 블로그가 드디어 개설했습니다!";
export const content = "안녕하세요, [유튜브](https://www.youtube.com/c/다이트로)에서 활동하는 다이트로라고 합니다.\n\n이번에 블로그로 제 일상이나 지식을 나눠주고 싶어가지고 블로그를 개설했습니다. 이 블로그는 Vue.js라는 라이브러리로 제작된 블로그입니다.\n\n# Vue.js\n[Vue.js](https://vuejs.org/)은 자바스크립트 프론트엔드 라이브러리 중 1개인데요. React.js, AngularJS랑 같이 자바스크립트 프론트엔드 라이브러리 3대장을 차지합니다.\n\n```html\n<div id=\"app\">\n  다이트로는 {{ adjective }}!\n</div>\n```\n```javascript\nvar app = new Vue({\n  el: '#app',\n  data: {\n    adjective: '재밌다'\n  }\n})\n```\n위처럼 코드를 작성할 경우, '다이트로는 재밌다!'라고 출력됩니다.\n\n# 마무리\n일단 Vue.js의 기본 개념과 설립했다는 걸 애기했네요. 다음엔 더욱 좋은 블로그로 찾겠습니다."
